body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: black;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

div{
  display: flex;
  justify-content: center;
  align-items: center;
}
fieldset{
  width: 350px;
  background-color: black;
  border: 2px solid red;
}
.TitleName{
  font-size: 20px;
  font-weight: 600;
  color: red;
}
.TimeDate{
  font-size: small;
  color: red;
  margin-left: 5px;
}
#img{
  width: 50px;
  height: 50px;
  border-radius: 50px;
  float: left;
  border: 1px solid red;
}

#quote{
  font-size: 16px;
  font-weight: 600;
  color: red;
}
#profileImg{
  width: 500px;
  border: 1px solid black;
}
#likeBtn{
  width: 100px;
  height: 30px;
  font-size: medium;
  font-weight: bold;
  border: 3px solid red;
  color: white;
  background-color: black;
}
#commentBtn{
  width: 100px;
  height: 30px;
  margin-left: 100px;
  font-size: 14px;
  font-weight: bold;
  border: none;
  color: white;
  background-color: black;
  border: 3px solid red;
}
#shareBtn{
  width: 100px;
  height: 30px;
  margin-left: 100px;
  font-size: 14px;
  font-weight: bold;
  border: none;
  color: white;
  background-color: black;
  border: 3px solid red;
}
hr{
  border-color: red;
}